export default{
	props:{
		maxlength:{
			type:Number,
			require:false,
			default:4,
		},
		isPwd:{
			type:Boolean,
			require:false,
			default:false,
		}
	},
	data(){
		return{
			codeIndex:1,
			codeArr:[],
			value:undefined,
		}
	},
	methods:{
		//取值
		getVal(e) {
			let { value } = e.detail;
			this.value=value;
			// console.log('验证码:', value);
			let arr = value.split('');
			this.codeIndex = arr.length + 1;
			this.codeArr = arr;
			if (this.codeIndex > Number(this.maxlength)) {
				//输入完成
				this.$emit('finish',true,this.value);
				// this.$emit('finish',this.codeArr.join(''));
			}else{
				this.$emit('finish',false,this.value);
			}
		},
		//清除验证码或者密码
		clear(){
			this.codeIndex=1;
			this.codeArr=[];
			this.val="";
		}
	}
}
